<template>
  <header class="header">
    <nav class="navbar" id="headerNav" :class="{ scrollBackground: scrollBg > 50 }">
      <div class="container">
        <router-link class="navbar__logo" to="/">
          <img src="@/assets/images/Logo.svg" alt="" />
        </router-link>
        <button class="navbar__menu" @click="burgerOn">
          <i class="fal fa-bars"></i>
        </button>
        <ul class="navbar__list" :class="{ active: burger }">
          <button class="navbar__close" @click="burgerOff">
            <i class="fal fa-times"></i>
          </button>
          <li v-for="link in list" :key="link.id">
            <router-link
              class="navbar__link"
              :to="link.path"
              @click="burgerOff"
            >
              {{ link.title }}
            </router-link>
          </li>
          <li>
            <router-link class="navbar__link" to="/search" @click="burgerOff">
              <i class="fal fa-search"></i>
            </router-link>
          </li>
        </ul>
      </div>
    </nav>
  </header>
</template>

<script setup>
import { reactive, ref, onMounted } from "vue";

let burger = ref(false);
let scrollBg = ref(0);

const burgerOn = () => {
  burger.value = true;
  document.body.style.overflow = "hidden";
};
const burgerOff = () => {
  burger.value = false;
  document.body.style.overflow = "visible";
};
const windowScroll = () => {
  scrollBg.value = window.scrollY;
};

const list = reactive([
  { id: 1, title: "Главная", path: "/" },
  { id: 2, title: "Фильмы", path: "/movie" },
  { id: 3, title: "Сериалы", path: "/tv" },
]);

onMounted(() => {
  window.addEventListener("scroll", windowScroll);
});
</script>
