import { defineStore } from "pinia";
import { baseUrl, lang, header_def } from "@/static";
import axios from "axios";

export const usePopular = defineStore("popular", {
  state: () => ({
    movie: null,
    tv: null,
    totalResults: 0,
  }),
  actions: {
    async getPopular(type, page = 1) {
      try {
        const res = await axios({
          method: "GET",
          url: `${baseUrl}/${type}/popular?language=${lang}&page=${page}`,
          headers: header_def,
        });

        if (type == "movie") this.movie = res.data.results;
        else this.tv = res.data.results;
        this.totalResults = res.data.total_results;
      } catch (error) {
        console.error("Error in popular.js");
      }
    },
  },
});
