<template>
  <section class="popular" v-if="content">
    <router-link class="popular__title" :to="`/${type}`">
      <span>{{ type == "movie" ? "Фильмы" : "Сериалы" }}</span>
      <i class="fal fa-chevron-right"></i>
    </router-link>
    <swiper
      :spaceBetween="25"
      :navigation="true"
      :modules="modules"
      :breakpoints="swiperOptions.breakpoints"
    >
      <swiper-slide
        class="popular__item"
        v-for="item in content"
        :key="item.id"
        @click="getInfo(item.id)"
      >
        <img v-lazy="imgUrl + item.poster_path" alt="" />
        <router-link class="popular__item-link" :to="`/${type}/${item.id}`" />
      </swiper-slide>
      <swiper-slide>
        <router-link :to="`/${type}`" class="popular__item">
          <i class="fal fa-chevron-right"></i>
          <span>{{ type == "movie" ? "Все фильмы" : "Все сериалы" }}</span>
        </router-link>
      </swiper-slide>
    </swiper>
    <div class="info" :class="{ active: infoOpen }" ref="info">
      <InfoBlock :current="current" :type="type" @close="closeItem" />
    </div>
  </section>
  <Loader v-else />
</template>

<script setup>
import { reactive, onMounted, computed, ref } from "vue";
import { usePopular } from "@/stores/popular";
import { imgUrl } from "@/static";
import { Swiper, SwiperSlide } from "swiper/vue";
import { Navigation } from "swiper";
import "swiper/scss";
import "swiper/scss/navigation";
import InfoBlock from "@/components/InfoBlock/InfoBlock.vue";
import { useDetails } from "@/stores/details";

const props = defineProps({
  type: String,
});
const popular = usePopular();
const details = useDetails();
const modules = reactive([Navigation]);
const content = computed(() =>
  props.type == "movie" ? popular.movie : popular.tv
);

const swiperOptions = ref({
  breakpoints: {
    320: {
      slidesPerView: 1,
    },
    576: {
      slidesPerView: 2,
    },
    900: {
      slidesPerView: 3,
    },
    1200: {
      slidesPerView: 4,
    },
    1450: {
      slidesPerView: 5,
    },
  },
});

let infoOpen = ref(false);
let current = ref(null);
let info = ref(null);

const getInfo = async (id) => {
  current.value = null;
  await details.getDetails(props.type, id);
  current.value = props.type == "movie" ? details.movieId : details.tvId;
  infoOpen.value = true;
  let infoTop = info.value.offsetTop;
  window.scrollTo({
    top: infoTop - headerNav.offsetHeight,
    behavior: "smooth",
  });
};

const closeItem = () => {
  infoOpen.value = false;
  current.value = null;
  props.type == "movie" ? (details.movieId = null) : (details.tvId = null);
};

onMounted(() => {
  popular.getPopular(props.type);
});
</script>
