import { defineStore } from "pinia";
import { baseUrl, lang, header_def } from "@/static";
import axios from "axios";

export const useActors = defineStore("actors", {
  state: () => ({
    actorsMovie: null,
    actorsTv: null,
  }),
  actions: {
    async getActors(type, id, count) {
      try {
        const res = await axios({
          method: "GET",
          url: `${baseUrl}/${type}/${id}/credits?language=${lang}`,
          headers: header_def,
        });
        const cast = res.data.cast;
        const array = [];

        cast.forEach((item) => {
          if (array.length < count) array.push(item);
        });

        if (type == "movie") this.actorsMovie = array;
        else this.actorsTv = array;
      } catch (error) {
        console.error("Error in actors.js");
      }
    },
  },
});
